import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Popover from '@material-ui/core/Popover'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import { ReactComponent as WarningIcon } from '../../images/radioWarning.svg'
import { ReactComponent as CheckIcon } from '../../images/radioCheck.svg'
import WithoutCrew from '../ModalWindows/WithoutCrew'
import Modal from '../Modal'
import {
  currentTrip,
  removeBusId,
  scheduleError,
} from '../../redux/schedule/scheduleActions'
import { deleteBus } from '../../services/apiMethods'
import { getAuth } from '../../redux/global/globalSelectors'
import { getSchedule } from '../../redux/schedule/scheduleSelectors'

const Bus = ({ bus, trip }) => {
  const dispatch = useDispatch()
  const auth = useSelector(getAuth)
  const schedule = useSelector(getSchedule)
  const [anchorEl, setAnchorEl] = useState(null)
  const [isModal, setIsModal] = useState(false)
  const openPopover = event => {
    setAnchorEl(event.currentTarget)
  }
  const closePopover = () => {
    setAnchorEl(null)
  }
  const toggleModal = e => {
    if (!isModal) e.stopPropagation()
    setIsModal(!isModal)
  }

  const open = Boolean(anchorEl)

  const addTrip = () => dispatch(currentTrip(trip, bus))
  const removeBus = async () => {
    try {
      const { status } = await deleteBus(bus.tripVehicleId, auth)
      if (status === 200) {
        const editTrip = schedule.find(
          el => el.id === trip.id && el.departure === trip.departure
        )
        const newTrip = {
          ...editTrip,
          vehicles: editTrip.vehicles.filter(i => i.tripVehicleId !== bus.tripVehicleId),
        }
        if (newTrip.vehicles.length === 0) delete newTrip['vehicles']
        const final = [
          ...schedule.filter(el => el.id !== trip.id || el.departure !== trip.departure),
          newTrip,
        ]
        dispatch(removeBusId(final))
      }
    } catch (error) {
      dispatch(scheduleError(error))
    }
  }
  const buss = window.btoa(JSON.stringify(bus))
  return (
    <>
      <BusContainer color={bus.tripVehicleId}>
        <BusBox onClick={openPopover}>
          <p>{bus.number}</p>
          <p>{`(${bus.capacity}/${bus.passengersCount})`}</p>
          {bus.hasOwnProperty('crew') ? (
            <button type="button">
              <CheckIcon />
            </button>
          ) : (
            <button type="button" onClick={toggleModal}>
              <WarningIcon />
            </button>
          )}
        </BusBox>
      </BusContainer>
      <Popover
        open={open}
        anchorEl={anchorEl}
        onClose={closePopover}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <StyledLink
          to={{
            pathname: `/crew`,
            search: `?departure=${trip.departure.split(' ').join('T')}&tripId=${
              trip.id
            }&bus=${buss}`,
          }}
          onClick={addTrip}
        >
          Экипаж
        </StyledLink>
        <PopoverButton onClick={removeBus}>Удалить</PopoverButton>
      </Popover>

      <Modal open={isModal} onClose={toggleModal}>
        <WithoutCrew onClose={toggleModal} />
      </Modal>
    </>
  )
}
export default Bus

const StyledLink = styled(Link)`
  padding: 20px;
  display: block;
  border-bottom: ${prop => prop.theme.border};
  color: ${prop => prop.theme.textColorDark};
`
const PopoverButton = styled.p`
  padding: 20px;
  border-bottom: ${prop => prop.theme.border};
  &:last-child {
    border-bottom: none;
  }
`
const BusContainer = styled.div`
  background-color: ${props => `#c4${props.color}a`};
  border-radius: 4px;
  box-shadow: 0 4px 4px ${prop => prop.theme.textColorLight};
  width: 100px;
  margin-right: 12px;
  margin-bottom: 12px;
  text-align: center;
  position: relative;
  & p {
    color: #fff;
    font-size: 18px;
  }
  & button {
    position: absolute;
    bottom: -9px;
    right: -5px;
    padding: 0;
    border: none;
    background-color: #fff;
    border-radius: 50%;
    width: 20px;
    height: 20px;
  }
`
const BusBox = styled.div`
  position: relative;
  padding: 6px;
`
