import React from 'react'
import styled from 'styled-components'
import { ReactComponent as WarningIcon } from '../../images/radioWarning.svg'
import { Button } from '../../stylesheets/mainStyled'

const WithoutBus = ({ onClose }) => {
  return (
    <Box>
      <WarningIcon width="50px" height="50px" />
      <Text>На рейс не назначен автобус</Text>
      <Button type="button" onClick={onClose}>
        OK
      </Button>
    </Box>
  )
}

export default WithoutBus

const Box = styled.div`
  width: 330px;
  padding: 40px 80px;
  text-align: center;
  border-radius: 8px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
`
const Text = styled.p`
  font-size: 18px;
  padding-top: 40px;
  margin-bottom: 40px;
`
