import styled from 'styled-components'
import { device } from '../../stylesheets/device'
import Trip from './Trip'

const Day = ({ day }) => {
  return (
    <DayItem>
      <FlexColumn>
        <WeekDay>{new Date(day.date).toLocaleString('ru', { weekday: 'short' })}</WeekDay>
        <NumDay>{new Date(day.date).toLocaleString('ru', { day: '2-digit' })}</NumDay>
      </FlexColumn>
      <TripsDay>
        {day.trips.map(trip => (
          <Trip key={trip.id} trip={trip} />
        ))}
      </TripsDay>
    </DayItem>
  )
}

export default Day

const DayItem = styled.div`
  padding-top: 10px;
  display: flex;
`
const FlexColumn = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 6px;
`
const TripsDay = styled.div`
  display: flex;
  flex-direction: column;
  width: 300px;
  @media ${device.mobileL} {
    width: 400px;
  }
`
const WeekDay = styled.p`
  color: ${prop => prop.theme.textColorLight};
`
const NumDay = styled.p`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: ${prop => prop.theme.mainColor};
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
`
