import moment from 'moment'
import { createSelector } from 'reselect'

export const getSchedule = state => state.schedule.schedule
export const getCrew = state => state.schedule.crew
export const getCurrentTrip = state => state.schedule.currentTrip
export const getbuses = state => state.schedule.buses
export const getPassengers = state => state.schedule.passengers

export const getFormatSchedule = createSelector(getSchedule, trip => {
  const amountDays = moment(trip[0].departure).endOf('month').format('DD')

  const arr = []
  for (let i = 1; i <= amountDays; i++) {
    const day = `0${i}`.slice(-2)
    const date = `${moment(trip[0].departure).format('YYYY-MM')}-${day}`
    if (trip.some(el => moment(el.departure).format('YYYY-MM-DD') === date)) {
      arr.push({
        date: date,
        trips: trip
          .filter(el => moment(el.departure).format('YYYY-MM-DD') === date)
          .sort((a, b) => {
            const A = new Date(a.departure.split(' ').join('T')).getTime()
            const B = new Date(b.departure.split(' ').join('T')).getTime()
            return A - B
          }),
        isTrips: true,
      })
    } else {
      arr.push({ date: date, trips: [], isTrips: false, without: day })
    }
  }

  return arr
})
