import React, { useState, useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import styled from 'styled-components'
import moment from 'moment'
import { getFormatSchedule } from '../redux/schedule/scheduleSelectors'
import { getScheduleMonth } from '../redux/schedule/scheduleOperations'
import Day from '../components/ScheduleContainer/Day'

const options = [
  { label: 'Январь', value: '01' },
  { label: 'Февраль', value: '02' },
  { label: 'Март', value: '03' },
  { label: 'Апрель', value: '04' },
  { label: 'Май', value: '05' },
  { label: 'Июнь', value: '06' },
  { label: 'Июль', value: '07' },
  { label: 'Август', value: '08' },
  { label: 'Сентябрь', value: '09' },
  { label: 'Октябрь', value: '10' },
  { label: 'Ноябрь', value: '11' },
  { label: 'Декабрь', value: '12' },
]
const currentMonth = new Date().toLocaleString('ru', { month: '2-digit' })

const getCurrentMonth = () => options.find(el => el.value === currentMonth)

const getMonth = () => {
  if (currentMonth === '01') {
    return [
      options.find(el => el.value === '12'),
      ...options.filter(el => {
        return +currentMonth - 1 <= +el.value && +el.value <= +currentMonth + 2
      }),
    ]
  }

  if (currentMonth <= 10) {
    return options.filter(el => {
      return +currentMonth - 1 <= +el.value && +el.value <= +currentMonth + 2
    })
  }
  if (currentMonth === '11') {
    return [
      ...options.filter(el => {
        return +currentMonth - 1 <= +el.value && +el.value <= +currentMonth + 2
      }),
      options.find(el => el.value === '01'),
    ]
  }
  if (currentMonth === '12') {
    return [
      ...options.filter(el => {
        return +currentMonth - 1 <= +el.value && +el.value <= +currentMonth + 2
      }),
      options.find(el => el.value === '01'),
      options.find(el => el.value === '02'),
    ]
  }

  return options
}
const month = getMonth()

const SchedulePage = () => {
  const dispatch = useDispatch()
  const schedule = useSelector(getFormatSchedule)

  const [queryMonth, setQueryMonth] = useState(getCurrentMonth)
  const scheduleInfo = useCallback(
    (from, to) => dispatch(getScheduleMonth(from, to)),
    [dispatch]
  )
  useEffect(() => {
    const from = moment().startOf('month').format('YYYY-MM-DD').split('-')
    const to = moment().endOf('month').format('YYYY-MM-DD').split('-')
    from.splice(1, 1, queryMonth.value)
    to.splice(1, 1, queryMonth.value)
    to.splice(2, 1, moment(to, 'YYYY-MM').daysInMonth()).join('-')
    to[2] = moment(to, 'YYYY-MM').daysInMonth()
    const queryFrom = from.join('-')
    const queryTo = to.join('-')

    scheduleInfo(queryFrom, queryTo)
  }, [queryMonth, scheduleInfo])

  const changeMonth = value => setQueryMonth(value)
  return (
    <Container>
      <StyledSelect
        value={queryMonth}
        classNamePrefix={'Select'}
        options={month}
        onChange={changeMonth}
      />
      {schedule.length > 0 &&
        schedule.map((el, idx) =>
          el.isTrips ? (
            <Day key={idx} day={el} />
          ) : (
            <NoTrips>
              {schedule[idx - 1].isTrips ? (
                <DataSpan>{schedule[idx].without}</DataSpan>
              ) : null}
              {schedule[idx - 1].isTrips && schedule[idx + 1].isTrips ? (
                <DataSpan> &mdash; Ничего не запланировано</DataSpan>
              ) : null}
              {!schedule[idx - 1].isTrips && !schedule[idx + 1].isTrips && null}
              {!schedule[idx - 1].isTrips && schedule[idx + 1].isTrips && (
                <DataSpan>
                  &nbsp;- {schedule[idx].without} &mdash; Ничего не запланировано
                </DataSpan>
              )}
            </NoTrips>
          )
        )}
    </Container>
  )
}
//
export default SchedulePage

const Container = styled.div`
  padding: 10px 20px;
`

const StyledSelect = styled(Select)`
  & .Select__control,
  & .Select__menu {
    width: 150px;
    border-color: ${prop => prop.theme.mainColor};
  }
  &.Select__menu {
  }
  & .Select__indicator-separator {
    display: none;
  }
  & .Select__indicator {
    color: ${prop => prop.theme.mainColor};
    &:hover {
      color: ${prop => prop.theme.mainColor};
    }
  }
`
const DataSpan = styled.span`
  font-size: 24px;
  color: #626262;
`
const NoTrips = styled.div`
  padding: 25px 0;
  display: inline-block;
`
