import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Popover from '@material-ui/core/Popover'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import { ReactComponent as ArrowDropdownIcon } from '../../images/arrowDropdown.svg'
import { ReactComponent as WarningIcon } from '../../images/radioWarning.svg'
import Modal from '../Modal'
import WithoutBus from '../ModalWindows/WithoutBus'
import { device } from '../../stylesheets/device'
import {
  excludeTrip,
  includeTrip,
  scheduleError,
} from '../../redux/schedule/scheduleActions'
import { toExcludeTrip, toIncludeTrip } from '../../services/apiMethods'
import { getAuth } from '../../redux/global/globalSelectors'
import { getSchedule } from '../../redux/schedule/scheduleSelectors'
import Bus from './Bus'
import RouteIsntActive from '../ModalWindows/RouteIsntActive'

const Trip = ({ trip }) => {
  const dispatch = useDispatch()
  const auth = useSelector(getAuth)
  const schedule = useSelector(getSchedule)
  const [anchorEl, setAnchorEl] = useState(null)
  const [isModal, setIsModal] = useState(false)
  const openPopover = event => {
    setAnchorEl(event.currentTarget)
  }
  const closePopover = () => {
    setAnchorEl(null)
  }
  const open = Boolean(anchorEl)

  const toggleModal = () => setIsModal(!isModal)

  const onExcludeTrip = async () => {
    try {
      const { status } = await toExcludeTrip(trip.departure.slice(0, 10), trip.id, auth)
      closePopover()

      if (status === 200) {
        const newTrip = {
          ...schedule.find(el => el.id === trip.id && el.departure === trip.departure),
          active: false,
        }
        const newSchedule = [
          ...schedule.filter(el => el.id !== trip.id || el.departure !== trip.departure),
          newTrip,
        ]

        dispatch(excludeTrip(newSchedule))
      }
    } catch (error) {
      dispatch(scheduleError(error))
    }
  }
  const onIncludeTrip = async () => {
    try {
      const { status } = await toIncludeTrip(trip.departure.slice(0, 10), trip.id, auth)
      closePopover()

      if (status === 200) {
        const newTrip = {
          ...schedule.find(el => el.id === trip.id && el.departure === trip.departure),
          active: true,
        }
        const newSchedule = [
          ...schedule.filter(el => el.id !== trip.id || el.departure !== trip.departure),
          newTrip,
        ]
        dispatch(includeTrip(newSchedule))
      }
    } catch (error) {
      dispatch(scheduleError(error))
    }
  }

  return (
    <InfoBox key={trip.id}>
      <Flex>
        <div style={{ display: 'flex', justifyContent: 'space-between' }}>
          <RouteTitle onClick={openPopover}>
            <Text>{trip.departure.split(' ')[1]}</Text>
            <NameBox active={trip.active}>
              <Text>{trip.name}</Text>
              <TextSmall>{trip.passengersCount} пассажиров</TextSmall>
            </NameBox>
            <IconBox>
              <ArrowDropdownIcon />
            </IconBox>
          </RouteTitle>
          {!trip.hasOwnProperty('vehicles') && (
            <IconBox>
              <WarningIcon onClick={toggleModal} />
            </IconBox>
          )}
        </div>
        <BusList>
          {trip.hasOwnProperty('vehicles') &&
            trip.vehicles.map(bus => <Bus key={bus.id} bus={bus} trip={trip} />)}
        </BusList>
      </Flex>
      <Popover
        open={open}
        anchorEl={anchorEl}
        onClose={closePopover}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <StyledLink
          to={{
            pathname: trip.passengersCount > 0 ? '/passengers' : '/schedule',
            search:
              trip.passengersCount > 0
                ? `?departure=${trip.departure.split(' ').join('T')}&tripId=${trip.id}`
                : '',
          }}
        >
          Список пассажиров
        </StyledLink>
        {!trip.passed && (
          <StyledLink
            to={{
              pathname: '/buses',
              search: `?departure=${trip.departure.split(' ').join('T')}&tripId=${
                trip.id
              }`,
            }}
          >
            Назначить автобус
          </StyledLink>
        )}
        {trip.active && !trip.passed && (
          <PopoverButton onClick={onExcludeTrip}>Отменить</PopoverButton>
        )}
        {!trip.active && !trip.passed && (
          <PopoverButton onClick={onIncludeTrip}>Включить</PopoverButton>
        )}
        {!trip.passed && <PopoverButton>Задерживается</PopoverButton>}
      </Popover>
      <Modal open={isModal} onClose={toggleModal}>
        {trip.active ? (
          <WithoutBus onClose={toggleModal} />
        ) : (
          <RouteIsntActive onClose={toggleModal} />
        )}
      </Modal>
    </InfoBox>
  )
}

export default Trip

const InfoBox = styled.div`
  margin-top: 40px;
  padding: 10px 0 0 0;
  border-top: ${prop => prop.theme.border};
`

const Flex = styled.div`
  display: flex;
  flex-direction: column;
`
const RouteTitle = styled.div`
  display: flex;
`

const Text = styled.p`
  font-size: 18px;
  margin-right: 10px;
`
const NameBox = styled.div`
  width: 160px;
  color: ${prop => (prop.active ? '#000' : '#c4c4c4')};
  @media ${device.mobileL} {
    width: auto;
  }
`
const IconBox = styled.div``
const TextSmall = styled.p`
  font-size: 14px;
`
const StyledLink = styled(Link)`
  padding: 20px;
  display: block;
  border-bottom: ${prop => prop.theme.border};
  color: ${prop => prop.theme.textColorDark};
`
const PopoverButton = styled.p`
  padding: 20px;
  border-bottom: ${prop => prop.theme.border};
  &:last-child {
    border-bottom: none;
  }
`
const BusList = styled.ul`
  display: flex;
  flex-wrap: wrap;
`
